import {Injectable} from "@angular/core";
import {AppHttp} from "./app-http.service";
import {AppConfig} from "./app-config.service";

@Injectable()
export class HKService {
  constructor(private appHttp: AppHttp, private appConfig: AppConfig) {
  }

  getServerTime() {
    return this.appHttp.post(this.appConfig.timeHost+"/index/server-time");
  }

  getTypeTable() {
    return this.appHttp.post("/index/type-tab");
  }
  //获取六合彩最新一期开奖结果
  getHKWinNumber(code) {
    return this.appHttp.post("/index/lottery-res", {code: code});
  }

  //获取六合彩开奖记录
  getHKRecord(code, date) {
    return this.appHttp.post("/lottery/history-api", {code: code, date: date, extra_type: 1});
  }

  //获取六合彩特码走势数据
  getHKSpecial(code, date) {
    return this.appHttp.post("/lottery/history-api", {code: code, date: date, extra_type: 2});
  }

  //获取六合彩正码走势数据
  getHKPositive(code, date) {
    return this.appHttp.post("/lottery/history-api", {code: code, date: date, extra_type: 3});
  }

  //获取六合彩生肖走势数据
  getHKZodiac(code, date) {
    return this.appHttp.post("/lottery/history-api", {code: code, date: date, extra_type: 4});
  }

  //获取六合彩总和分析数据
  getHkSumAnalysis(code, date) {
    return this.appHttp.post("/lottery/history-api", {code: code, date: date, extra_type: 5});
  }

  //获取六合彩露珠分析数据
  getHkLuzhuAnalysis(code, date, type) {
    return this.appHttp.post("/chart/index", {code: code, year: date, type: type});
  }

  //六合资料列表-详情
  getNewsData(code, id?: string, page?: number, pageSize?: number) {
    return this.appHttp.post("/news/news-datas", {code: code, id: id, page: page, page_size: pageSize});
  }
  //开奖日历
  getHkCalendar(date){
    return this.appHttp.post("/index/lottery-date",{date:date});
  }

}



